
nav{
    background-color: #303030;
    display: flex;
    justify-content: right;
}
.nav-links-container{
    display: flex;
    justify-content: right;
    margin-right: 100px;
    z-index: 2000;
}
.nav-link{
    color: white;
    margin-right: 19px;
    font-size: 15px;
    width: 158px;
    padding-top: 15px;
    padding-bottom: 15px;
    font-weight: 600;
    cursor: pointer;
}

.nav-options{
    color: white;
    padding-right: 20px;
    font-size: 15px;
    font-weight: 600;
    width: 190px;
    padding-top: 13px;
    padding-bottom: 13px;
    background-color: #cfb995;
    display: flex;
    padding-left: 20px;
    padding-right: 20px;
    cursor: pointer;
}

.nav-options > div:nth-child(1){
    margin-right: 15px;
    padding-top: 2px;
}

.nav-options > div:nth-child(2){
    margin-right: 15px;
}

.nav-options > div:nth-child(3){
    padding-top: -2px;
}

.nav-symbol{
    margin-right: 20px;
}