.footer{
    height: 194px;
    padding-left: 10%;
    text-align: left;
    position: relative;
}

.footer-body{
    width: 180px;
    position: absolute;
    transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    top: 50%;
}

.footer-text{
    font-size: 16px;
    color: #f9f9f9;
    margin-bottom: 20px;
}

.footer-link{
    display: flex;
    color: #cfb995;
    font-weight: 600;
    font-size: 14px;
}

.footer-link> div:nth-child(1){
    margin-right: 7px;
}

.footer-link> div:nth-child(2){
    padding-top: 2px;
}